.grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.gridItem {
  flex-grow: 0;
}

.movie {
  width: 100px;
  text-align: center;
  margin: 10px;
  cursor: pointer;
}

.movie img {
  width: 100%;
}

.noImage {
  width: 100px;
  height: 140px;
  border: 1px solid #ccc;
  background: repeating-linear-gradient(
    -55deg,
    #222,
    #222 10px,
    #333 10px,
    #333 20px
  );
}

.movieText {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
